import React, { useEffect, useState } from "react";
import {
  LOCATION_CITY_SEARCH_API,
  FORECAST_5DAYS_API,
  IMAGE,
} from "../config/config";
import Masting from "./masting";

const PUBLIC_URL = "http://localhost:3000/src/images";

export default function Forecast() {
  const [city, setCity] = useState("");
  const [data, setData] = useState({});
  const [infoForecast, setInfoForecast] = useState({});

  useEffect(() => {
    const url = `${LOCATION_CITY_SEARCH_API}apikey=QGVG9Vsobr9AqAIzQywUbADjw6K9EhS6&q=${city}&details=true`;

    const getCity = async () => {
      const Cityresult = await fetch(url, {
        mode: "no-cors",
        credentials: "omit",
      }).then((res) => res.json());

      if (Cityresult[0]) {
        setData(Cityresult[0]);
      }
      console.log(Cityresult[0]);
    };
    getCity();
    //setCity("");
  }, [city]);

  useEffect(() => {
    const url = `${FORECAST_5DAYS_API}${data.Key}?apikey=QGVG9Vsobr9AqAIzQywUbADjw6K9EhS6&details=true`;

    const getInfo = async () => {
      const info = await fetch(url, {
        mode: "no-cors",
        credentials: "omit",
      }).then((res) => res.json());

      if (info.Headline) {
        setInfoForecast(info);
      }
      console.log(info);
    };
    getInfo();
  }, [data]);

  const SearchCity = ({ target }) => {
    console.log(target.value);
    setCity(target.value);
  };

  const Split = (str) => {
    console.log(str);
    let slash = "/";
    let res = str.split("T");
    let res1 = res[0].split("-");
    return res1[1].concat(slash, res1[2]);
  };

  const AddPath = (num) => {
    let strPath = IMAGE.concat(num, ".png");
    console.log(strPath);
    return strPath;
  };

  const ConvertToCelsius = (num) => {
    const a = 32;
    const b = 5;
    const c = 9;
    let celsius = Math.round((num - a) * (b / c)).toString();
    let res = celsius.concat("°");
    return res;
  };

  return (
    <div className="min-h-screen">
      <h2 className="text-xl font-semibold underline mt-6">
        5 Days of Daily Forecasts
      </h2>
      <form className="flex justify-center items-center mt-6">
        <label htmlFor="city">City name: </label>
        <input
          className="text-black ml-2 focus:outline-none"
          type="search"
          name="city"
          id="city"
          value={city}
          onChange={SearchCity}
        />
      </form>
      <Masting data={data} />

      <div className="mt-8">
        {infoForecast.DailyForecasts ? (
          <ul className="list-none mt-6">
            <li className="flex flex-col md:flex-row justify-center items-center p-2 bg-white text-black w-2/4 rounded mx-auto">
              <p>{Split(infoForecast.DailyForecasts[0].Date)}</p>
              <img
                src={
                  infoForecast.DailyForecasts[0].Day
                    ? AddPath(infoForecast.DailyForecasts[0].Day.Icon)
                    : null
                }
                alt={
                  infoForecast.DailyForecasts[0].Day
                    ? infoForecast.DailyForecasts[0].Day.IconPhrase
                    : "Image not found"
                }
                width="128px"
                height="128px"
              ></img>
              <span>
                {infoForecast.DailyForecasts[0].Temperature
                  ? ConvertToCelsius(
                      infoForecast.DailyForecasts[0].Temperature.Maximum.Value
                    )
                  : "-"}
              </span>
              <span className="text-sm text-gray-600">
                /
                {infoForecast.DailyForecasts[0].Temperature
                  ? ConvertToCelsius(
                      infoForecast.DailyForecasts[0].Temperature.Minimum.Value
                    )
                  : "-"}
              </span>
              <span className="ml-5">
                {infoForecast.DailyForecasts[0].Day
                  ? infoForecast.DailyForecasts[0].Day.IconPhrase
                  : "-"}
              </span>
            </li>
            {/* 111111111111 */}
            <li className="flex flex-col md:flex-row justify-center items-center p-2 bg-white text-black w-2/4 rounded mx-auto mt-1">
              <p>{Split(infoForecast.DailyForecasts[1].Date)}</p>
              <img
                src={
                  infoForecast.DailyForecasts[1].Day
                    ? AddPath(infoForecast.DailyForecasts[1].Day.Icon)
                    : null
                }
                alt={
                  infoForecast.DailyForecasts[1].Day
                    ? infoForecast.DailyForecasts[1].Day.IconPhrase
                    : "Image not found"
                }
                width="128px"
                height="128px"
              ></img>
              <span>
                {infoForecast.DailyForecasts[1].Temperature
                  ? ConvertToCelsius(
                      infoForecast.DailyForecasts[1].Temperature.Maximum.Value
                    )
                  : "-"}
              </span>
              <span className="text-sm text-gray-600">
                /
                {infoForecast.DailyForecasts[1].Temperature
                  ? ConvertToCelsius(
                      infoForecast.DailyForecasts[1].Temperature.Minimum.Value
                    )
                  : "-"}
              </span>
              <span className="ml-5">
                {infoForecast.DailyForecasts[1].Day
                  ? infoForecast.DailyForecasts[1].Day.IconPhrase
                  : "-"}
              </span>
            </li>
            {/* 222222222 */}
            <li className="flex flex-col md:flex-row justify-center items-center p-2 bg-white text-black w-2/4 rounded mx-auto mt-1">
              <p>{Split(infoForecast.DailyForecasts[2].Date)}</p>
              <img
                src={
                  infoForecast.DailyForecasts[2].Day
                    ? AddPath(infoForecast.DailyForecasts[2].Day.Icon)
                    : null
                }
                alt={
                  infoForecast.DailyForecasts[2].Day
                    ? infoForecast.DailyForecasts[2].Day.IconPhrase
                    : "Image not found"
                }
                width="128px"
                height="128px"
              ></img>
              <span>
                {infoForecast.DailyForecasts[2].Temperature
                  ? ConvertToCelsius(
                      infoForecast.DailyForecasts[2].Temperature.Maximum.Value
                    )
                  : "-"}
              </span>
              <span className="text-sm text-gray-600">
                /
                {infoForecast.DailyForecasts[2].Temperature
                  ? ConvertToCelsius(
                      infoForecast.DailyForecasts[2].Temperature.Minimum.Value
                    )
                  : "-"}
              </span>
              <span className="ml-5">
                {infoForecast.DailyForecasts[2].Day
                  ? infoForecast.DailyForecasts[2].Day.IconPhrase
                  : "-"}
              </span>
            </li>
            {/* 33333333 */}
            <li className="flex flex-col md:flex-row justify-center items-center p-2 bg-white text-black w-2/4 rounded mx-auto mt-1">
              <p>{Split(infoForecast.DailyForecasts[3].Date)}</p>
              <img
                src={
                  infoForecast.DailyForecasts[3].Day
                    ? AddPath(infoForecast.DailyForecasts[3].Day.Icon)
                    : null
                }
                alt={
                  infoForecast.DailyForecasts[3].Day
                    ? infoForecast.DailyForecasts[3].Day.IconPhrase
                    : "Image not found"
                }
                width="128px"
                height="128px"
              ></img>
              <span>
                {infoForecast.DailyForecasts[3].Temperature
                  ? ConvertToCelsius(
                      infoForecast.DailyForecasts[3].Temperature.Maximum.Value
                    )
                  : "-"}
              </span>
              <span className="text-sm text-gray-600">
                /
                {infoForecast.DailyForecasts[3].Temperature
                  ? ConvertToCelsius(
                      infoForecast.DailyForecasts[3].Temperature.Minimum.Value
                    )
                  : "-"}
              </span>
              <span className="ml-5">
                {infoForecast.DailyForecasts[3].Day
                  ? infoForecast.DailyForecasts[3].Day.IconPhrase
                  : "-"}
              </span>
            </li>
            {/* 44444444 */}
            <li className="flex flex-col md:flex-row justify-center items-center p-2 bg-white text-black w-2/4 rounded mx-auto mt-1">
              <p>{Split(infoForecast.DailyForecasts[4].Date)}</p>
              <img
                src={
                  infoForecast.DailyForecasts[4].Day
                    ? AddPath(infoForecast.DailyForecasts[4].Day.Icon)
                    : null
                }
                alt={
                  infoForecast.DailyForecasts[4].Day
                    ? infoForecast.DailyForecasts[4].Day.IconPhrase
                    : "Image not found"
                }
                width="128px"
                height="128px"
              ></img>
              <span>
                {infoForecast.DailyForecasts[4].Temperature
                  ? ConvertToCelsius(
                      infoForecast.DailyForecasts[4].Temperature.Maximum.Value
                    )
                  : "-"}
              </span>
              <span className="text-sm text-gray-600">
                /
                {infoForecast.DailyForecasts[4].Temperature
                  ? ConvertToCelsius(
                      infoForecast.DailyForecasts[4].Temperature.Minimum.Value
                    )
                  : "-"}
              </span>
              <span className="ml-5">
                {infoForecast.DailyForecasts[4].Day
                  ? infoForecast.DailyForecasts[4].Day.IconPhrase
                  : "-"}
              </span>
            </li>
          </ul>
        ) : null}
      </div>
    </div>
  );
}
