import React, { useState } from "react";
import Nav from "./nav";
import { NavLink } from "react-router-dom";

export default function Header() {
  const [menu, setMenu] = useState("hidden");

  const navFunc = () => {
    const active = menu === "hidden" ? "block" : "hidden";
    setMenu(active);
  };

  return (
    <header className="w-full">
      <div className="topnav">
        <div className="block py-3 text-2xl font-bold border-bottom">
          <NavLink to="/">
            NELLY METEO <i className="far fa-laugh-beam text-yellow-600"></i>
          </NavLink>
        </div>
        <button
          className="icon w-2/12 md:w-1/12 focus:outline-none"
          onClick={navFunc}
        >
          {menu === "hidden" ? (
            <i className="fa fa-bars"></i>
          ) : (
            <i className="fas fa-times"></i>
          )}
        </button>
        <Nav menu={menu} />
      </div>
    </header>
  );
}

