import React from "react";

export default function mainHome() {
  return (
    <>
      <img
        className="w-full object-cover min-h-screen"
        src="https://picsum.photos/400/600?random=1"
        alt="ImageBG"
      />
      <div className="content">
        <h1 className="font-bold text-xl">
          Nelly Meteo App <i className="fas fa-cloud-sun text-white"></i>
        </h1>
        <p>
          Questa app consente di vedere le previsoni del meteo per qualunque
          città
        </p>
      </div>
    </>
  );
}
